import React from "react"

import { motion, useMotionValue, useTransform } from "framer-motion"

const Joystick = () => {
  const x = useMotionValue(0)
  const y = useMotionValue(0)
  const rotateX = useTransform(x, [-200, 200], [-200, 200])
  const rotateY = useTransform(y, [-200, 200], [200, -200])
  return (
    <div>
      <motion.div
        style={{
          width: 150,
          height: 150,
          borderRadius: "50%",
          perspective: 200,
          background: "radial-gradient(rgba(255,255,255,0), rgba(0,0,0,0.5))",
        }}
      >
        <motion.div
          style={{
            width: 50,
            height: 50,
            borderRadius: "10px",
            background: "#05f",
            z: 100,
          }}
          drag
          dragConstraints={{ left: 50, top: 50, right: 50, bottom: 50 }}
          dragElastic={0.1}
        />
      </motion.div>
    </div>
  )
}

export default Joystick
